<template>
	<view class="rate flex align-end justify-start" @click="clickRate">
		<block><view v-for="item in fulls" :key="item" class="star bg-img star-full" /></block>
		<view v-if="half" class="star bg-img star-half" />
		
		<!-- #ifdef H5 -->
		<block><text v-for="star in nos" :key="star+'_star'"  class="star bg-img star-no" /></block>
		<!-- #endif -->
		
		<!-- #ifdef MP-WEIXIN -->
		<block><text v-for="star in nos" :key="star"  class="star bg-img star-no" /></block>
		<!-- #endif -->
		
		<text v-if="showText" class="text">
			<text>{{ score }}</text>
			åˆ†
		</text>
	</view>
</template>

<script>
export default {
	name: 'Rate',
	props: {
		score: {
			type: [String , Number],
			default: 0
			//required: true
		},
		disabled: {
			type: Boolean,
			default: true
		},
		showText: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			fulls: 0,
			half: 0,
			nos: 5,
			curScore: 5
		};
	},
	created: function() {
		this.getStars();
	},
	/**
  mounted() {
	const query = uni.createSelectorQuery().in(this)
	query.select('.rate').boundingClientRect(data => {
		if(data.left) this.position.left = data.left
		if(data.top) this.position.top = data.top
	}).exec()
  },
  */
	methods: {
		getStars(curScore) {
			const score = curScore || this.score;
			if (score > 5 || score < 0) {
				return;
			} else {
				let fs = Math.floor(score);
				let hs = 1;
				const decimal = score - fs;
				if (decimal <= 0.25) {
					hs = 0;
				}
				if (decimal > 0.5) {
					fs++;
					hs = 0;
				}
				this.fulls = fs;
				this.half = hs;
				this.nos = 5 - fs - hs;
				if (!this.disabled) {
					this.$emit('getRateStars', hs / 2 + fs);
				}
			}
		},
		clickRate(e) {
			if (!this.disabled) {
				const rateLeft = e.currentTarget.offsetLeft;
				const targetLeft = e.detail.x;
				const dx = targetLeft - rateLeft;
				this.curScore = dx / 21;
				this.getStars(this.curScore);
			}
		}
	}
};
</script>

<style lang="scss">
.rate {
	.vw {
		display: inline-block;
	}
	.text {
		@include sc(24upx, $mc-greylight);
		margin-left: 10upx;
		font-weight: 400;
		height: 32upx;
		text {
			font-size: 28upx;
			margin-right: 4upx;
		}
	}
	.star {
		width: 32upx;
		height: 32upx;
		display: inline-block;
		margin-right: 10upx;
		&:last-child {
			margin-right: 0;
		}
		&.star-full {
			background-image: url('data:image/png;base64,');
		}
		&.star-half {
			background-image: url('data:image/png;base64,');
		}
		&.star-no {
			background-image: url('data:image/png;base64,');
		}
	}
}
</style>
